.zantimes-post-grid {
  padding-top: 30px;
  padding-bottom: 30px;
}
.zantimes-post-grid .featured-img {
  height: auto;
  width: 100%;
  object-fit: cover;
  object-position: left center;
  border-radius: 8px;
}
.zantimes-post-grid .post-meta {
  color: #1D0056;
  margin-top: 0.75em;
  margin-bottom: 0.5em;
}
.zantimes-post-grid .post-meta a {
  color: #1D0056;
  text-decoration: none;
}
.zantimes-post-grid .post-meta span:before {
  content: "";
  height: 10px;
  width: 10px;
  background-color: #FFBE06;
  border-radius: 100%;
  display: inline-block;
  margin-left: 10px;
  margin-right: 15px;
}
.zantimes-post-grid .post-link {
  text-decoration: none;
  color: #000000;
}
@media screen and (min-width: 992px) {
  .zantimes-post-grid {
    padding-top: 50px;
    padding-bottom: 50px;
  }
  .zantimes-post-grid .post-title {
    max-width: 362px;
  }
}
@media screen and (max-width: 991px) {
  .zantimes-post-grid__col ~ .zantimes-post-grid__col {
    margin-top: 1.5rem;
  }
  .zantimes-post-grid__col img {
    margin-bottom: 1rem;
  }
}